.developer-wrapper {
  margin-top: 25px;
}

.developer-heading {
  text-align: center;
}

.experience h5 {
  font-weight: bold;
  margin-top: 20px;
}

.experience h5 a {
  text-decoration: none;
  color: inherit;
}

.experience span {
  font-style: italic;
}

.tech-icon {
  height: 100px;
  width: 100px;
}

@media only screen and (max-width: 768px) {
  .developer-heading h3 {
    font-size: 125%;
  }
  .experience h5 {
    font-size: 90%;
  }
  .experience span {
    font-size: 75%;
  }
  .experience ul {
    padding-left: 20px;
  }
  .experience li {
    font-size: 60%;
  }
  .tech-icon {
    height: 50px;
    width: 50px;
  }
}